import { and, or } from '../re'
import { proto } from '../object'

export const compiler = (expr使 = '{', expr屎 = '}', ctrl使 = '{#', ctrl屎 = '#}') => {
  const expr = and()(expr使, /(?<expr>[^]*?)/, expr屎)
  const ctrl = and()(ctrl使, /(?<ctrl>[^]*?)/, ctrl屎)

  // match the longest tag first to avoid ambiguity
  const re = expr使.length > ctrl使.length
    ? or('ug')(expr, ctrl)
    : or('ug')(ctrl, expr)

  return (macro: string, scope = {}, ref = '$', acc = '危') => {
    let ops = ''

    for (let op = '=', cursor = 0;;) {
      const match = re.exec(macro)
      const part = match
        ? macro.slice(cursor, cursor = match.index)
        : macro.slice(cursor)

      ops += op + JSON.stringify(part)

      if (!match)
        break

      const { expr, ctrl } = match.groups!
      ops += ctrl ? `;${ctrl};` : `+(${expr})`
      op = ctrl ? `${acc}+=` : '+'
      cursor += match[0].length
    }

    const body = `let ${acc}${ops};return ${acc}`
    const fn = new Function(`{${[...proto(scope)]}}`, ref, body)
    return fn.bind(null, scope) as (context?: {}) => string
  }
}

export const compile = compiler()
